@media only screen and (max-width: 600px) {
  .pagination > li {
    padding: 0;
  }
}

.pagination > li:nth-child(4) {
  background: #fff0f0;
  border: 1px solid #fdd;
  margin: 0;
  > select {
    background: rgba(0, 0, 0, 0);
    border: 0;
    color: #444;
    font-size: 1.2rem;
    height: 100%;
    outline: none;
    padding: 0 .8rem 0 0;
  }
}